import React, { useState, useEffect } from "react";
import TopicSelector from "./components/TopicSelector";
import QuestionDisplay from "./components/QuestionDisplay";
import { generateQuestion } from "./utils/questionGenerator";
import { onAuthStateChanged, signOut } from "firebase/auth";
import { auth } from "./firebaseConfig";
import LoginForm from "./components/LoginForm";
import RegisterForm from "./components/RegisterForm";
import "./App.css";

function App() {
  const [topic, setTopic] = useState("addition");
  const [question, setQuestion] = useState(generateQuestion("addition"));
  const [userAnswer, setUserAnswer] = useState("");
  const [feedback, setFeedback] = useState("");
  const [user, setUser] = useState(null);

  // Track user session
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (firebaseUser) => {
      setUser(firebaseUser);
    });
    return () => unsubscribe();
  }, []);

  const handleTopicChange = (newTopic) => {
    setTopic(newTopic);
    setQuestion(generateQuestion(newTopic));
    setUserAnswer("");
    setFeedback("");
  };

  const handleSubmit = () => {
    if (parseFloat(userAnswer) === question.answer) {
      setFeedback(" Correct!");
    } else {
      setFeedback(` Wrong. Correct answer is ${question.answer}`);
    }
    setTimeout(() => {
      setQuestion(generateQuestion(topic));
      setUserAnswer("");
      setFeedback("");
    }, 2000);
  };

  const handleLogout = () => {
    signOut(auth).then(() => setUser(null));
  };

  return (
    <div className="app-container">
      <h1>Home Tutor App</h1>

      {!user ? (
        <>
          <LoginForm onLogin={setUser} />
          <RegisterForm onRegister={setUser} />
        </>
      ) : (
        <>
          <p> Welcome, {user.email} <button onClick={handleLogout}>Logout</button></p>
          <TopicSelector topic={topic} onChange={handleTopicChange} />
          <QuestionDisplay
            question={question}
            userAnswer={userAnswer}
            setUserAnswer={setUserAnswer}
          />
          <button onClick={handleSubmit}>Submit</button>
          <p className="feedback">{feedback}</p>
        </>
      )}
    </div>
  );
}

export default App;

